import {GameRandom, Inject, ModelEvents, Singleton} from "@denishussar/html5-game-engine";
import { Item } from "./Item";


export enum GameStateEnum {
    none,
    preview,
    menu,
    game,
    shop,
}
@Singleton
export class GameModel {
    //public player = new PlayerModel();

    constructor(@Inject private _$random: GameRandom,
                @Inject private _$modelEvents: ModelEvents,
    ) {
    }

    public state: GameStateEnum = GameStateEnum.preview;

    public itemsMaxCountinTheBox = 5;
    public itemsOnShelves: Array<Item> = [];
    public itemsInTheBox: Array<Item> = [];
    public itemsInTheFireplace: Array<Item> = [];
}

