import {
    AnimationBaseController,
    GameRandom,
    SoundType,
} from "@denishussar/html5-game-engine";
import { Inject } from "@denishussar/html5-game-engine";
import { IAnimationControllerConfiguration } from "@denishussar/html5-game-engine";
import { Graphics, Sprite, Texture } from "pixi.js";
import { CollisionComponent } from "../../ash/components/CollisionComponent";
import { PhysicsBodyComponent } from "../../ash/components/PhysicsBodyComponent";
import { PositionComponent } from "../../ash/components/PositionComponent";
import { Creator } from "../../ash/Creator";
import { SoundConstants, SoundLevels } from "../../constants/SoundConstants";
import { GameLogic } from "../../logic/GameLogic";
import { GameModel, GameStateEnum } from "../../models/GameModel";
import { Item } from "../../models/Item";
import { AnimationContents } from "../AnimationContents";
import { ButtonController, IButtonControllerOptions } from "../Common/ButtonController";
import { Easing } from "../Common/Easing";
import { EmptyAnimationController } from "../Test/EmptyAnimationController";

export interface IBoxControllerOptions {
}

export class BoxController extends AnimationBaseController {
    $model: GameModel;

    private creator: Creator;
    private easings: Array<Easing> = []

    public constructor(@Inject private _options: IBoxControllerOptions,
        @Inject private _random: GameRandom,
        @Inject private _gameLogic: GameLogic) {
        super("BoxController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {

        return {
            animationContent: AnimationContents.LEVEL_CONTENT_JSON3,
            libraryName: "main_screen_container2",
            childControllers: {
                cardbox: {
                    instanceName: 'main_screen2.cardbox',
                    controller: EmptyAnimationController,
                },
                sparcle: {
                    instanceName: 'main_screen2.box_sparcle2',
                    controller: EmptyAnimationController,
                }
                
            },
            modelEvents: {
                // 1.   ,   
                // 2.    ,   -  
                changeGameState: (value: GameStateEnum) => {
                    
                },
                removeItemFromTheBox: (value: Item) => {
                    this.updateSparcles();
                },
                addItemToTheBox: (value: Item) => {
                    this.updateSparcles();
                },
            },
            sounds: [
                // {
                //     id: SoundConstants.AMBIENT,
                //     type: SoundType.Music
                // }
            ]
        };
    }

    protected onInitialize(): void {
        // this.soundManager.play(SoundConstants.AMBIENT, true, SoundLevels.levelAmbient);
        setTimeout(() => {
            this.updateSparcles();
        }, 1000);
    }

    protected onResize(): void {
        this.updateSparcles();
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
        this.easings.forEach((easing) => {
            easing.update(frameRate * 1000);
        });
    }

    private updateSparcles(): void {
        
        const sparcle = this.$childControllers.get('sparcle').display;
        if (!sparcle) {
            return;
        }
        
        const max = this._gameLogic.getMaxItemsCountInTheBox();
        const count = this.$model.itemsInTheBox.length;
        const easing = new Easing(
            [sparcle.alpha], [count === 0 ? 0 : count / max], 300
        );
        easing.onStep = (values) => {
            sparcle.alpha = values[0];
        }
        easing.onComplete = () => {
            for (let i = 0; i < this.easings.length; i++) {
                if (easing === this.easings[i]) {
                    this.easings.splice(i, 1);
                    return;
                }
            }
        }
        this.easings.push(easing);
    }

}

