import { Engine, Entity } from "@ash.ts/ash";
import { Container, Sprite } from "pixi.js";
import { CollisionComponent } from "./components/CollisionComponent";
import { PhysicsBodyComponent } from "./components/PhysicsBodyComponent";
import { PositionComponent } from "./components/PositionComponent";
import { SpriteComponent } from "./components/SpriteComponent";
import { PhysicsSystem } from "./systems/PhysicsSystem";
import { SpriteSystem } from "./systems/SpriteSystem";
import { SystemPriorities } from "./systems/SystemPriorities";
import { DeadSystem } from "./systems/DeadSystem";
import { DeadComponent } from "./components/DeadComponent";
import { PhysicsMouseJointComponent } from "./components/PhysicsMouseJointComponent";
import * as planck from 'planck-js';
import { Item } from "../models/Item";

export class Creator {
    engine: Engine;
    world: planck.World;
    constructor(public container: Container) {
        let gravity = planck.Vec2(0, 8);
        this.world = planck.World(gravity);

        this.engine = new Engine();

        this.engine.addSystem(new SpriteSystem(this.container), SystemPriorities.render);
        this.engine.addSystem(new PhysicsSystem(this, this.world), SystemPriorities.resolveCollisions);
        this.engine.addSystem(new DeadSystem(this.engine), SystemPriorities.update);
    }

    public update(time: number): void {
        this.engine.update(time);
    }

    public createSprite(sprite: Sprite, position: PositionComponent | undefined = undefined): void {
        const entity = new Entity();

        entity.add(new SpriteComponent(sprite));
        entity.add(position ? position : new PositionComponent());

        
        this.engine.addEntity(entity);
    }

    public createWall(x: number, y: number, width: number, height: number) {
        let box = this.world.createBody();
        // a body can have one or more fixtures. This is how we create a box fixture inside a body
        box.createFixture(planck.Box(width, height));
        // now we place the body in the world
        box.setPosition(planck.Vec2(x, y));
        // time to set mass information
        box.setMassData({
            mass: 1,
            center: planck.Vec2(),
            // I have to say I do not know the meaning of this "I", but if you set it to zero, bodies won't rotate
            I: 1
        });
    }

    public createPhysicsBody(
        sprite: Sprite, 
        position: PositionComponent | undefined = undefined,
        collision: CollisionComponent | undefined = undefined,
        physicsBody: PhysicsBodyComponent | undefined = undefined,
        item: Item
    ): void {
        const entity = new Entity();

        entity.add(new SpriteComponent(sprite))
        entity.add(position ? position : new PositionComponent())
        entity.add(collision ? collision : new CollisionComponent(10))
        entity.add(physicsBody ? physicsBody : new PhysicsBodyComponent());
        entity.add(new DeadComponent(10000 * item.aliveFactor))

        // sprite.interactive = true;
        // sprite.buttonMode = true;
        // sprite.addListener('pointerdown', () => {
        //     entity.add(new PhysicsMouseJointComponent());
        // });
        // sprite.addListener('pointerup', () => {
        //     entity.remove(PhysicsMouseJointComponent);
        // });
        // sprite.addListener('pointerupoutside', () => {
        //     entity.remove(PhysicsMouseJointComponent);
        // });

        this.engine.addEntity(entity);
    }
}