import {
    AnimationBaseController,
    GameRandom, IAnimationControllerConfiguration, Inject, SoundType
} from "@denishussar/html5-game-engine";
import { SoundConstants, SoundLevels } from "../../constants/SoundConstants";
import { GameLogic } from "../../logic/GameLogic";
import { GameModel, GameStateEnum } from "../../models/GameModel";
import { AnimationContents } from "../AnimationContents";
import { Easing } from "../Common/Easing";
import { TimeSkipper } from "../Common/TimeSkipper";
import { EmptyAnimationController } from "../Test/EmptyAnimationController";

export interface IPreviewSceneControllerOptions {
}

export class PreviewSceneController extends AnimationBaseController {
    $model: GameModel;

    public constructor(@Inject private _options: IPreviewSceneControllerOptions,
        @Inject private _random: GameRandom,
        @Inject private _gameLogic: GameLogic) {
        super("PreviewSceneController");
        console.log('PreviewSceneController')
    }

    private children = [];
    private easings: Array<Easing> = [];
    
    protected getConfiguration(): IAnimationControllerConfiguration {

        return {
            animationContent: AnimationContents.LEVEL_CONTENT_JSON4,
            libraryName: "main_screen_container4",
            childControllers: {
                s1: {
                    instanceName: 'main_screen4.s1',
                    controller: EmptyAnimationController
                },
                s2: {
                    instanceName: 'main_screen4.s2',
                    controller: EmptyAnimationController
                },
                s3: {
                    instanceName: 'main_screen4.s3',
                    controller: EmptyAnimationController
                },
                s4: {
                    instanceName: 'main_screen4.s4',
                    controller: EmptyAnimationController
                },
                s5: {
                    instanceName: 'main_screen4.s5',
                    controller: EmptyAnimationController
                },
            },
            onPointerDown: () => {
                this._gameLogic.changeGameState(GameStateEnum.game);
            },
            modelEvents: {
                changeGameState: (value: GameStateEnum) => {
                    if (value !== GameStateEnum.preview) {
                        this.display.visible = false;
                    }
                }
            }
        };
    }

    protected onInitialize(): void {
        for (let i = 1; i < 6; i++) {
            this.children.push(this.$childControllers.get(`s${i}`));
        }

        for (let i = 1; i < this.children.length; i++) {
            this.children[i].visible = false;
        }

        this.playIntro();
    }

    private async playIntro() {
        await new TimeSkipper(2000).execute()
        await this.changeImage(1);
        await new TimeSkipper(2000).execute()
        await this.changeImage(2);
        await new TimeSkipper(2000).execute()
        await this.changeImage(3);
        await new TimeSkipper(2000).execute()
        await this.changeImage(4);
        await new TimeSkipper(2000).execute()
        .then(() => {
            if (this.$model.state === GameStateEnum.preview) {
                this._gameLogic.changeGameState(GameStateEnum.game);
            }
        }) 
    }

    private async changeImage(index: number) {
        return new Promise((resolve) => {
            for (let i = 0; i < this.children.length; i++) {
                this.children[i].visible = false;
                if (i === index) {
                    this.children[i].visible = true;
                }
            }
            resolve(true);
        });
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
        this.easings.forEach((easing) => {
            easing.update(frameRate * 1000);
        });
    }
}

