import { Engine, ListIteratingSystem } from "@ash.ts/ash";

import { DeadNode } from "../nodes/DeadNode";

export class DeadSystem extends ListIteratingSystem<DeadNode> {
  constructor(private engine: Engine) {
    super(DeadNode);
  }
  updateNode(node: DeadNode, delta: number) {
    node.dead.alive -= delta;
    if (node.dead.alive <= 0) {
      this.engine.removeEntity(node.entity);
    } else {
        switch (Math.floor(10 * (1 - node.dead.alive / node.dead.maxAlive))) {
            case 1:
                node.sprite.view.tint = 0xffeeee;
            break;
            case 2:
                node.sprite.view.tint = 0xffdddd;
            break;
            case 3:
                node.sprite.view.tint = 0xffcccc;
            break;
            case 4:
                node.sprite.view.tint = 0xffbbbb;
            break;
            case 5:
                node.sprite.view.tint = 0xff9999;
            break;
            case 6:
                node.sprite.view.tint = 0xff7777;
            break;
            case 7:
                node.sprite.view.tint = 0xff5555;
            break;
            case 8:
                node.sprite.view.tint = 0xff3333;
            break;
            case 9:
                node.sprite.view.tint = 0xff0000;
            break;
            case 10:
                node.sprite.view.tint = 0xff0000;
            break;
        }
    }
  }
}
