import {
    AnimationBaseController,
    GameRandom,
    SoundType,
} from "@denishussar/html5-game-engine";
import { Inject } from "@denishussar/html5-game-engine";
import { IAnimationControllerConfiguration } from "@denishussar/html5-game-engine";
import { Graphics, Point, Sprite, Texture, Text } from "pixi.js";
import { CollisionComponent } from "../../ash/components/CollisionComponent";
import { PhysicsBodyComponent } from "../../ash/components/PhysicsBodyComponent";
import { PositionComponent } from "../../ash/components/PositionComponent";
import { Creator } from "../../ash/Creator";
import { PICTURE_IMG } from "../../Constants/PictureConstants";
import { SoundConstants, SoundLevels } from "../../constants/SoundConstants";
import { GameLogic } from "../../logic/GameLogic";
import { GameModel, GameStateEnum } from "../../models/GameModel";
import { Item } from "../../models/Item";
import { AnimationContents } from "../AnimationContents";
import { ButtonController, IButtonControllerOptions } from "../Common/ButtonController";
import { Easing } from "../Common/Easing";
import { Utils } from "../Common/Utils";
import { EmptyAnimationController } from "../Test/EmptyAnimationController";
import * as penner from "penner";
import { DeadNode } from "../../ash/nodes/DeadNode";
import { TimeSkipper } from "../Common/TimeSkipper";

export interface IMainScreenControllerOptions {
}

export class MainScreenController extends AnimationBaseController {
    $model: GameModel;

    private X = -284;
    private Y = -319;
    private WIDTH = 631;
    private HEIGHT = 785;

    private BOX_X = 200;
    private BOX_Y = 250;
    private BOX_WIDTH = 170;
    private BOX_HEIGHT = 130;

    private creator: Creator;
    private easings: Array<Easing> = [];
    
    private boxItem: {sprite: Sprite, easing: Easing} | undefined;
    private dragging: {sprite: Sprite, startPoint: Point, mouseLocalStartPoint: Point} | undefined = undefined;
    private lastMouseLocalPosition = new Point();

    public constructor(@Inject private _options: IMainScreenControllerOptions,
        @Inject private _random: GameRandom,
        @Inject private _gameLogic: GameLogic) {
        super("MainScreenController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {

        return {
            animationContent: AnimationContents.LEVEL_CONTENT_JSON,
            libraryName: "main_screen_container",
            childControllers: {
                fire: {
                    instanceName: 'main_screen.fire',
                    controller: EmptyAnimationController
                },
                shop_btn: {
                    instanceName: 'main_screen.shop_btn',
                    controller: ButtonController,
                    options: {
                        onClick: () => {
                            this._gameLogic.changeGameState(GameStateEnum.shop);
                        }
                    } as IButtonControllerOptions
                },
                scoop_btn: {
                    instanceName: 'main_screen.scoop_btn',
                    controller: ButtonController,
                    options: {
                        onClick: () => {}
                    } as IButtonControllerOptions
                },
                poker_btn: {
                    instanceName: 'main_screen.poker_btn',
                    controller: ButtonController,
                    options: {
                        onClick: () => {}
                    } as IButtonControllerOptions
                },
                cardbox: {
                    instanceName: 'main_screen.cardbox',
                    controller: EmptyAnimationController,
                    // options: {
                    //     onClick: () => {}
                    // } as IButtonControllerOptions
                },
                buble: {
                    instanceName: 'main_screen.buble',
                    controller: EmptyAnimationController,
                    // options: {
                    //     onClick: () => {}
                    // } as IButtonControllerOptions
                },
                life_timer: {
                    instanceName: 'main_screen.life_timer',
                    controller: EmptyAnimationController,
                    // options: {
                    //     onClick: () => {}
                    // } as IButtonControllerOptions
                },
                life_bar_component: {
                    instanceName: 'main_screen.life_bar_component',
                    controller: EmptyAnimationController,
                    // options: {
                    //     onClick: () => {}
                    // } as IButtonControllerOptions
                },
            },
            onPointerDown: (e) => {
                this.lastMouseLocalPosition.x = e.localPosition.x
                this.lastMouseLocalPosition.y = e.localPosition.y;
            },
            onPointerMove: (e) => {
                this.lastMouseLocalPosition.x = e.localPosition.x
                this.lastMouseLocalPosition.y = e.localPosition.y;
            },
            modelEvents: {
                changeGameState: (value: GameStateEnum) => {
                    if (value === GameStateEnum.game) {
                        this.display.visible = true;

                        if (!this.soundManager.isSoundPlaying(SoundConstants.AMBIENT)) {
                            this.soundManager.play(SoundConstants.AMBIENT, true, SoundLevels.levelAmbient);
                        }

                        this.updateTheBoxItem();
                    } else {
                        this.display.visible = false;
                        this.soundManager.stop(SoundConstants.AMBIENT);

                        this.updateTheBoxItem();
                    }
                },
                addItemToTheBox: (value: Item) => {
                    this.updateTheBoxItem();
                },
                removeItemFromTheBox: (value: Item) => {
                    this.updateTheBoxItem();
                },
                removeItemFromTheFire: () => {
                    new TimeSkipper(3000).execute()
                        .then(() => {
                            if (this.$model.itemsInTheFireplace.length === 0) {
                                this.$childControllers.get('buble').visible = true;
                            }
                        })
                },
                addItemToTheFire: () => {
                    new TimeSkipper(1000).execute()
                        .then(() => {
                            this.$childControllers.get('buble').visible = false;
                        })
                }
            },
            sounds: [
                {
                    id: SoundConstants.AMBIENT,
                    type: SoundType.Music
                }
            ]
        };
    }

    protected onInitialize(): void {
        this.setupFireplace();
        this.setupFire();

        this.$childControllers.get('buble').visible = false;
        this.$childControllers.get('life_timer').visible = false;
        

        setTimeout(() => {
            this.updateTheBoxItem();    
        }, 1000);

        new TimeSkipper(3000).execute()
            .then(() => {
                if (this.$model.itemsInTheFireplace.length === 0) {
                    this.$childControllers.get('buble').visible = true;
                }
            })
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
        if (this.creator) {
            this.creator.update(frameRate * 1000);
        }
        if (this.dragging) {
            
            const positionX = this.dragging.startPoint.x + this.lastMouseLocalPosition.x - this.dragging.mouseLocalStartPoint.x;
            const positionY = this.dragging.startPoint.y + this.lastMouseLocalPosition.y - this.dragging.mouseLocalStartPoint.y;
            this.dragging.sprite.x = positionX;
            this.dragging.sprite.y = positionY;
        }
        if (this.easings) {
            this.easings.forEach((easing) => {
                easing.update(frameRate);
            })
        }
    }

    private setupFire(): void {
        const template = this.$childControllers.get('fire').display;
        
        const easing = new Easing(
            [template.alpha, template.scale.y], 
            [Utils.getRandomArbitrary(0.6, 0.9), Utils.getRandomArbitrary(0.9, 1.1)],
            Utils.getRandomInt(50, 100)
            );
        easing.onComplete = () => {
            for (let i = 0; i < this.easings.length; i++) {
                if (this.easings[i] === easing) {
                    this.easings.splice(i, 1);
                    this.setupFire();
                    break;
                }
            }
        };
        easing.onStep = (values: Array<number>) => {
            try {
                template.alpha = values[0];
                template.scale.set(values[1]);
            } catch (err) {}
        };
        this.easings.push(easing);
    }

    private setupFireplace(): void {
        const template = this.$childControllers.get('fire').display;
        const container = new Sprite(Texture.EMPTY);
        container.position.set(this.X, this.Y);
        template.parent.addChildAt(container, template.parent.getChildIndex(template) - 1);
        
        this.creator = new Creator(container);
        const worldScale = 30;
        const wall = 40;
        
        this.creator.createWall(this.WIDTH / 2 / worldScale, (this.HEIGHT + wall / 2) / worldScale, this.WIDTH / worldScale, wall / worldScale);
        this.creator.createWall(-wall / 2 / worldScale, this.HEIGHT / 2 / worldScale, wall / worldScale, this.HEIGHT / worldScale);
        this.creator.createWall((this.WIDTH + wall / 2) / worldScale, this.HEIGHT / 2 / worldScale, wall / worldScale, this.HEIGHT / worldScale);

        const deadlist = this.creator.engine.getNodeList(DeadNode);
        deadlist.nodeRemoved.add(this.handleItemDestroied);
        
        // this.debug();
    }

    private handleItemDestroied = (node: DeadNode) => {
        const item = this._gameLogic.getItemById(node.sprite.view.name);
        if (item) {
            this._gameLogic.removeItemFromTheFire(item);
        }

        if (this.$model.itemsInTheFireplace.length === 0) {
            //  
            this.$childControllers.get('buble').visible = true;
            setTimeout(() => {
                this.$childControllers.get('buble').visible = false;
            }, 3000)
        }
        
    }

    private updateTheBoxItem(): void {
        if (this.boxItem) {
            for (let i = 0; i < this.easings.length; i++) {
                if (this.boxItem.easing === this.easings[i]) {
                    this.easings.splice(i, 1);
                }
            }
            this.boxItem.sprite.parent.removeChild(this.boxItem.sprite);
            this.boxItem = undefined;
        }
        
        if (this.$model.state === GameStateEnum.game) {
            if (this.$model.itemsInTheBox.length > 0) {
                const item = this.$model.itemsInTheBox[this.$model.itemsInTheBox.length - 1];
                const image = PICTURE_IMG[`${item.id}`];
                const sprite = Sprite.from(image);
                sprite.scale.set(.5)
                sprite.anchor.set(.5);
                sprite.position.set(
                    this.BOX_X + this.BOX_WIDTH / 2, 
                    this.BOX_Y - this.BOX_HEIGHT / 2
                );
                sprite.name = item.id;
                sprite.interactive = true;
                sprite.buttonMode = true;
                sprite.addListener('pointerdown', (e) => {
                    this.startDragItem(e);
                });
                sprite.addListener('pointerup', (e) => {
                    this.stopDragItem(e);
                });
                sprite.addListener('pointerupoutside', (e) => {
                    this.stopDragItem(e);
                });

                this.display.addChild(sprite);
                
                this.boxItem = {
                    sprite,
                    easing: undefined
                };

                this.setupBoxItemAnimation();
            }
        }
    }

    private startDragItem(e: PIXI.interaction.InteractionEvent): void {
        // lock interaction
        this.boxItem = undefined;

        this.lastMouseLocalPosition.x = e.data.getLocalPosition(this.display).x;
        this.lastMouseLocalPosition.y = e.data.getLocalPosition(this.display).y;

        const sprite = e.target as Sprite;
        sprite.parent.setChildIndex(sprite, sprite.parent.children.length - 1);
        const startPoint = sprite.position.clone();
        const mouseLocalStartPoint = new Point(this.lastMouseLocalPosition.x, this.lastMouseLocalPosition.y);
        this.dragging = {sprite, startPoint, mouseLocalStartPoint: mouseLocalStartPoint};

        const item = this._gameLogic.getItemById(sprite.name);
        if (item) {
            this.showDescription(item);
        }
    }

    private stopDragItem(e): void {
        if (!this.dragging) {
            return;
        }

        const sprite = this.dragging.sprite;
        if (
            sprite.x > this.X + 100
            && sprite.x < this.X + this.WIDTH - 100
            && sprite.y < this.Y + this.HEIGHT
        ) {
            //   -   
            sprite.interactive = false;
            sprite.buttonMode = false;

            const width = sprite.texture.width * sprite.scale.y;
            const height = sprite.height;
            
            const position = this.creator.container.toLocal(sprite.parent.toGlobal(sprite.position));
            const item = this._gameLogic.getItemById(sprite.name);
            if (item) {
                this._gameLogic.removeItemFromTheBox(item);
                this._gameLogic.addItemToTheFire(item);

                this.creator.createPhysicsBody(
                    sprite,
                    new PositionComponent(position.x, position.y),
                    new CollisionComponent(width),
                    new PhysicsBodyComponent(PhysicsBodyComponent.SHAPE_BOX),
                    item
                )
            }
            
        } else {
            //    
            const easing = new Easing(
                [sprite.x, sprite.y], 
                [this.dragging.startPoint.x, this.dragging.startPoint.y],
                300,
                penner.easeInCirc
            )
            easing.onStep = (values: Array<number>) => {
                sprite.position.x = values[0];
                sprite.position.y = values[1];
            }
            easing.onComplete = () => {
                for (let i = 0; i < this.easings.length; i++) {
                    if (this.easings[i] === easing) {
                        this.easings.splice(i, 1);
                        sprite.parent.removeChild(sprite);
                        this.updateTheBoxItem();
                        break;
                    }
                }
            }
            this.easings.push(easing);
        }

        this.dragging = undefined;
    }

    private setupBoxItemAnimation(): void {
        if (!this.boxItem) {
            return;
        }

        for (let i = 0; i < this.easings.length; i++) {
            if (this.boxItem.easing === this.easings[i]) {
                this.easings.splice(i, 1);
            }
        }

        const startPosition = this.BOX_Y - this.BOX_HEIGHT / 2;
        const oStartPosition = Math.abs(this.boxItem.sprite.position.y - startPosition) < 5;
        const easing = new Easing(
            [this.boxItem.sprite.position.y], 
            [oStartPosition ? startPosition - 20 : startPosition],
            oStartPosition ? 500 : 500,
            oStartPosition ? penner.easeOutCirc : penner.easeInCirc
            );
        easing.onComplete = () => {
            for (let i = 0; i < this.easings.length; i++) {
                if (this.easings[i] === easing) {
                    this.easings.splice(i, 1);
                    this.setupBoxItemAnimation();
                    break;
                }
            }
        };
        easing.onStep = (values: Array<number>) => {
            try {
                this.boxItem.sprite.position.y = values[0];
            } catch (err) {}
        };
        this.boxItem.easing = easing;
        this.easings.push(easing);
    }

    private showDescription(value: Item): void {
        if (value.description === '') {
            return;
        }

        const fontSize = 36;
        const label = new PIXI.Text(value.description,{fontFamily : 'Arial', fontSize: fontSize, fill : 0xffffff, align : 'center'});
        label.anchor.set(0.5);

        const g = new Graphics();
        g.beginFill(0xffffff);
        g.alpha = .2;
        g.drawRoundedRect(0, 0, label.width + 25, label.height + 25, 25);
        g.endFill();

        const sprite = new Sprite(g.generateCanvasTexture());
        sprite.addChild(label);
        sprite.anchor.set(0.5);
        
        this.display.addChild(sprite);

        value.description = '';

        const easing = new Easing(
            [sprite.position.y, 1], 
            [sprite.position.y - this.HEIGHT / 2, .3], 
            8000, 
            penner.easeOutCubic
        )

        easing.onStep = (values) => {
            sprite.position.y = values[0];
            sprite.alpha = values[1];
        }

        easing.onComplete = () => {
            sprite.parent.removeChild(sprite);
            for (let i = 0; i < this.easings.length; i++) {
                if (easing === this.easings[i]) {
                    this.easings.splice(i, 1);
                    break;
                }
            }
        }
        this.easings.push(easing);

    }

    private debug(): void {
        setInterval(() => {
            const width = 100;
            const height = 100;

            let g = new Graphics();
            g.beginFill(0xff0000);
            g.drawRect(0, 0, width, height);
            g.endFill();

            this.creator.createPhysicsBody(
                new Sprite(g.generateCanvasTexture()),
                new PositionComponent(Utils.getRandomInt(width, this.WIDTH - width), -100),
                new CollisionComponent(width),
                new PhysicsBodyComponent(PhysicsBodyComponent.SHAPE_BOX),
                new Item()
            )
        }, 2000);
    }
}

