import {
    GameInstanceBase,
    Inject,
    IPreloadersConfiguration,
    ISimpleControllerConfiguration,
} from "@denishussar/html5-game-engine";
import {GameModel} from "./models/GameModel";
import {IRootControllerOptions, RootController} from "./RootController";
import {IVersionControllerOptions, VersionController} from "./VersionController";
import {PreloaderGameController} from "./controllers/Common/Preloader/PreloaderGameController";
import {hook} from "./utils/Hooks";

export class GameInstance extends GameInstanceBase {
    @Inject private $model: GameModel;

    public constructor(
        @Inject options: any,
    ) {
        super();

        this.shadowUpdateEnabled = true;
    }

    protected onContentLoaded() {
        super.onContentLoaded();
    }

    protected onCreate(): void {
        hook.viewport = this._container; //todo: refactor 0_o
    }

    protected getPreloadersConfiguration(): IPreloadersConfiguration {
        return {
            childControllers: {
                loading: {
                    controller: PreloaderGameController, //todo: not good
                    options: {
                        preloader: false, //todo: separate and refactor
                    },
                },
                preloader: {
                    controller: PreloaderGameController,
                    options: {
                        preloader: true,
                    },
                },
            },
        };
    }

    protected getConfiguration(): ISimpleControllerConfiguration {
        return {
            childControllers: {
                rootController: {
                    controller: RootController,
                    model: this.$model,
                    options: <IRootControllerOptions>{
                        version: this.version,
                    },
                },
                version: {
                    controller: VersionController,
                    options: <IVersionControllerOptions>{
                        version: this.version,
                    },
                },
            },
        };
    }

    protected onLandscape(): void {
    }

    protected onInitialize(): void {
    }

    protected onPortrait(): void {
    }
}
