import {GameRandom, Inject, ModelEvents, Singleton} from "@denishussar/html5-game-engine";
import {GameModel, GameStateEnum} from "../models/GameModel";
import {GameStorageManager} from "@denishussar/html5-game-engine/dist";
import { Item } from "../models/Item";

@Singleton
export class GameLogic {

    constructor(@Inject private _model: GameModel,
                @Inject private _random: GameRandom,
                @Inject private _modelEvents: ModelEvents,
                @Inject private _storageManager: GameStorageManager,
    ) {
        console.log("GameLogic", this);
    }

    public getMaxItemsCountInTheBox(): number {
        return this._model.itemsMaxCountinTheBox;
    }

    public changeGameState(value: GameStateEnum): void {
        const oldState = this._model.state;
        this._model.state = value;
        this._modelEvents.dispatch(this._model, 'changeGameState', this._model.state, oldState);
    }

    public getItemById(id: string): Item | undefined {
        const arrs = [
            this._model.itemsInTheBox,
            this._model.itemsOnShelves,
            this._model.itemsInTheFireplace
        ]
        for (let i = 0; i < arrs.length; i++) {
            for (let a = 0; a < arrs[i].length; a++) {
                if (id === arrs[i][a].id) {
                    return arrs[i][a];
                }
            }
        }
        return undefined;
    }

    public addItemToTheFire(item: Item): void {
        this._model.itemsInTheFireplace.push(item);
        this._modelEvents.dispatch(this._model, 'addItemToTheFire', item);
    }

    public removeItemFromTheFire(item: Item): void {
        for (let i = 0; i < this._model.itemsInTheFireplace.length; i++) {
            if (this._model.itemsInTheFireplace[i] === item) {
                this._model.itemsInTheFireplace.splice(i, 1);
                this._modelEvents.dispatch(this._model, 'removeItemFromTheFire', item);
                break;
            }
        }
    }

    public removeItemFromTheBox(item: Item): void {
        for (let i = 0; i < this._model.itemsInTheBox.length; i++) {
            if (this._model.itemsInTheBox[i] === item) {
                this._model.itemsInTheBox.splice(i, 1);
                this._modelEvents.dispatch(this._model, 'removeItemFromTheBox', item);
                break;
            }
        }
    }

    public addItemToTheBox(item: Item): void {
        this._model.itemsInTheBox.push(item);
        this._modelEvents.dispatch(this._model, 'addItemToTheBox', item);
    }

    public removeItemFromShelves(item: Item): void {
        for (let i = 0; i < this._model.itemsOnShelves.length; i++) {
            if (this._model.itemsOnShelves[i] === item) {
                this._model.itemsOnShelves.splice(i, 1);
                this._modelEvents.dispatch(this._model, 'removeItemFromShelves', item);
                break;
            }
        }
    }
}
