import { Engine, NodeList, System } from "@ash.ts/ash";
import * as planck from 'planck-js';
import { MouseJoint, Vec2 } from "planck-js";
import { PhysicsBodyComponent } from "../components/PhysicsBodyComponent";
import { Creator } from "../Creator";
import { PhysicsBodyNode } from "../nodes/PhysicsBodyNode";
import { PhysicsMouseJointNode } from "../nodes/PhysicsMouseJointNode";

export class PhysicsSystem extends System {

    private physicsBodies: NodeList<PhysicsBodyNode>;
    private physicsJoints: NodeList<PhysicsMouseJointNode>;

    private gameOptions = {
 
        // world scale to convert Box2D meters to pixels
        worldScale: 30,
     
        // game gravity
        gameGravity: 8,
     
        // amount of boxes when the game starts
        startingBoxes: 16,
     
        // box size, in pixels
        boxSize: 100,
     
        // delay between two boxes, in milliseconds
        boxDelay: 1500,
     
        // time before box value is hidden, in milliseconds
        timeBeforeHide: 2000
    }

    constructor(public creator: Creator, public world: planck.World) {
        super();
    }

    addToEngine(engine: Engine): void {
        // this.createWorld();

        this.physicsBodies = engine.getNodeList(PhysicsBodyNode);
        this.physicsBodies.nodeAdded.add(this.physicsBodyAdded);
        this.physicsBodies.nodeRemoved.add(this.physicsBodyRemoved);

        this.physicsJoints = engine.getNodeList(PhysicsMouseJointNode);
        this.physicsJoints.nodeAdded.add(this.physicsMouseJointAdded);
        this.physicsJoints.nodeRemoved.add(this.physicsMouseJointRemoved);

        for (let node = this.physicsBodies.head; node; node = node.next) {
            this.physicsBodyAdded(node);
        }
    }

    removeFromEngine(engine: Engine): void {
        this.physicsBodies.nodeAdded.remove(this.physicsBodyAdded);
        this.physicsBodies.nodeRemoved.remove(this.physicsBodyRemoved);
        this.physicsBodies = undefined;

        this.physicsJoints.nodeAdded.add(this.physicsMouseJointAdded);
        this.physicsJoints.nodeRemoved.add(this.physicsMouseJointRemoved);
        this.physicsJoints = undefined;
    }

    update(time: number): void {
        this.world.step(1 / 30);
        this.world.clearForces();

        for (let node = this.physicsBodies.head; node; node = node.next) {
            const position = node.physicsBody.body.getPosition();
            node.position.x = position.x * this.gameOptions.worldScale;
            node.position.y = position.y * this.gameOptions.worldScale;
            node.position.rotation = node.physicsBody.body.getAngle();
        }
    }

    private physicsMouseJointAdded = (node: PhysicsMouseJointNode): void => {
        const mouseJoint = new MouseJoint(
            {maxForce: 1000}, 
            this.world.createBody(), 
            node.physicsBody.body, 
            new Vec2({x: 0, y:0}));
        this.world.createJoint(mouseJoint);

        node.joint.mouseJoint = mouseJoint;
        mouseJoint.setTarget(new Vec2({x: 100, y: 100}));
    }

    private physicsMouseJointRemoved = (node: PhysicsMouseJointNode): void => {
        this.world.destroyJoint(node.joint.mouseJoint);
    }

    private physicsBodyAdded = (node: PhysicsBodyNode): void => {

        if (node.physicsBody.type === PhysicsBodyComponent.SHAPE_CIRCLE) {
            const body = this.world.createDynamicBody();
            body.setPosition(planck.Vec2(node.position.x / this.gameOptions.worldScale, node.position.y / this.gameOptions.worldScale));
            body.createFixture(planck.Circle(node.collision.radius / this.gameOptions.worldScale), {
                friction: 0,
                restitution: 0
            });
            node.physicsBody.body = body;
        } else {
            node.physicsBody.body = this.createBox(
                node.position.x, 
                node.position.y,
                node.collision.radius, 
                node.collision.radius,
                true
            )
        }
    }

    private physicsBodyRemoved = (node: PhysicsBodyNode): void => {
        this.world.destroyBody(node.physicsBody.body);
    }

    createBox(posX: number, posY: number, width: number, height: number, isDynamic: boolean): planck.Body {
 
        // this is how we create a generic Box2D body
        let box = this.world.createBody();
        if (isDynamic) {
 
            // Box2D bodies born as static bodies, but we can make them dynamic
            box.setDynamic();
        }
 
        // a body can have one or more fixtures. This is how we create a box fixture inside a body
        box.createFixture(planck.Box(width / 2 / this.gameOptions.worldScale, height / 2 / this.gameOptions.worldScale));
 
        // now we place the body in the world
        box.setPosition(planck.Vec2(posX / this.gameOptions.worldScale, posY / this.gameOptions.worldScale));
 
        // time to set mass information
        box.setMassData({
            mass: 1,
            center: planck.Vec2(),
 
            // I have to say I do not know the meaning of this "I", but if you set it to zero, bodies won't rotate
            I: 1
        });

        if (isDynamic) {
            box.setDynamic();
        }

        return box;
    }
   
}