import {Game} from "@denishussar/html5-game-engine";
import {GameInstance} from "./GameInstance";
import * as WebFont from "webfontloader";

declare var require;

//require("../fonts/fonts.css");
const gameCfg = require("../game-config.json");
const version = require("../package.json").version;

export default class main {
    constructor() {
        WebFont.load({
            custom: {
                families: [
                    "Times New Roman",
                    "Arial Black",
                    "Montserrat",
                    "Myriad Pro",
                    "MyriadPro-Regular",
                    "MyriadPro-Bold",
                ],
            },
            loading: () => {
                this.startGame();
            },
        });

    }

    public startGame(): Game {
        return new Game({
            main: GameInstance,
            version: version,
            assetsRoot: gameCfg.resourcesRoot,
            elementId: "gamePlace",
            noWebGL: false,
            backgroundColor: 0x06263b,
            clearBeforeRender: true,
            transparent: false,
            showStats: false,
            disableContextMenu: true,
            antialias: false,
            fps: 60,
            gameId: gameCfg.gameId,
            sounds:[
                {
                    "id": "ambient",
                    "path": "/sounds/ambient_soundtrack.wav"
                },
                {
                    "id": "guitar",
                    "path": "/sounds/guitar_soundtrack.wav"
                }]
        });
    }

}

new main();
